export default {
	data() {
		return {
			id:'',
			totals:{},
			xpTypes:[],
			list:[],
			pageIndex:1,
			pageSize:20,
			pageTotal:1,
			nodata:false,
			nomore:false
		};
	},
	onLoad(options){
		this.type=options.type
	},
	onShow() {
		this.accountInfo()
		this.getXPTypes()
	},
	methods:{
		loadMore(){
			if(this.pageIndex<this.pageTotal){
				this.pageIndex++
				this.getList()
			}else{
				this.nodata=true
			}
		},
		accountInfo(){  //1.人名币2.星票3.活跃度
			let self =this
			let params = {
				type:this.type
			}
			this.$acFrame.HttpService.accountNums(params).then(res=>{
				if(res.success){
					self.totals = res.data
				}
			})
		},
		getList(){
			let self = this
			let params={
				type:this.type,
				pageIndex:this.pageIndex,
				pageSize:this.pageSize
			}
			this.$acFrame.HttpService.accountInfo(params).then(res=>{
				if(res.success){
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if(list.length>0){
						list.filter(fv=>{
							if(fv.bizTime){
								fv.bizTime = self.$acFrame.Util.formatTime(fv.bizTime,'dayhm')
							}
							self.xpTypes.forEach((i,cv)=>{
								if(fv.bizType == cv.key){
									fv.typeName=cv.val
									fv.itemType=cv.extendData
								}
							})
						})
						self.list =self.list.concat(list)
					}else{
						self.nodata=true
					}
					
				}
			})
		},
		getXPTypes(){
			let self= this
			let xpTypes=uni.getStorageSync('xpTypes')
			if(xpTypes){
				this.xpTypes = xpTypes
				this.getList()
			}else{
				this.$acFrame.HttpService.getXPTypes().then(res=>{
					if(res.success){
						self.xpTypes = res.data
						uni.setStorageSync('xpTypes',res.data)
						self.getList()
					}
				})
			}
				
			
		}
	}
}
